html, body {
    font-family: 'Roboto', sans-serif;
    padding: 0;
    margin: 0;
  }
  
  #root {
    height: 100vh;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .Outer {
    display: flex;
    justify-content: center;
    text-align: center;
    height: 100vh;
    align-items: center;
    background-image: url('../../icons/second.jpg');
    background-position: center;
    background-size: contain;
  }
  
  .Inner {
    width: 20%;
  }
  
  .Join {
    border-radius: 0;
    padding: 15px 20px;
    width: 100%;
  }
  
  .heading {
    color:  #2979FF;
    font-size: 2.5em;
    padding-bottom: 10px;
    border-bottom: 2px solid  #2979FF;
  }
  
  .button {
      color: #fff !important;
      text-transform: uppercase;
      text-decoration: none;
      background: #2979FF;
      padding: 20px;
      border-radius: 5px;
      display: inline-block;
      border: none;
      width: 100%;
  }
  
  .mt-20 {
    margin-top: 20px;
  }
  
  @media (min-width: 320px) and (max-width: 480px) {
    .Outer {
      height: 100%;
    }
  
    .Inner {
      width: 90%;
    }
  }
  
  button:focus {
    outline: 0;
  }